<!--ListMode.vue -->
<template>
  <div class="content-area">
    <div class="restaurant-container">
      <div class="content-wrapper">
        <!-- Filter Controls -->
        <div class="filter-container">
          <!-- City Filter -->
          <div class="dropdown">
            <select id="city-select" v-model="localSelectedCity" @change="updateTownOptions">
              <option value="">請選擇縣市</option>
              <option v-for="city in cityOptions" :key="city" :value="city">{{ city }}</option>
            </select>
          </div>
          <!-- Town Filter -->
          <div class="dropdown">
            <select id="town-select" v-model="localSelectedTown">
              <option value="">請選擇鄉鎮市區</option>
              <option v-for="town in townOptions" :key="town" :value="town">{{ town }}</option>
            </select>
          </div>
        </div>

        <!-- Restaurant Items -->
        <div v-for="item in pagedData" :key="item.ID" class="restaurant-item">
          <div class="location-info">
            <!-- City Label -->
            <div class="city-label highlight-city">{{ item.City }}</div>
            <!-- Town Label -->
            <div class="town-label">{{ item.Town }}</div>
          </div>
          <!-- Restaurant Content -->
          <div class="restaurant-content">
            <!-- Restaurant Image -->
            <div class="restaurant-image">
              <div class="image-container">
                <img :src="item.PicURL" alt="Restaurant Image">
              </div>
            </div>
            <!-- Restaurant Information -->
            <div class="restaurant-info">
              <!-- Restaurant Name -->
              <div class="restaurant-title">
                <div class="restaurant-name" :style="{ color: item.Url ? 'rgb(12, 164, 211)' : 'black' }" @click="openUrl(item.Url)">{{ item.Name }}</div>
                <!-- Restaurant Description -->
                <div class="restaurant-description" v-html="item.FoodFeature.replace(/<br><br>/g, '')"></div>
              </div>
            </div>
          </div>
        </div>

        <!-- Pagination -->
        <div class="pagination">
          <!-- Page Info -->
          <div class="page-info">美食頁次 {{ currentPage }} / {{ totalPages }}</div>
          <!-- Page Buttons -->
          <div class="page-buttons">
            <button v-for="pageNumber in pageNumbers" :key="pageNumber" @click="gotoPage(pageNumber)" :disabled="currentPage === pageNumber" class="page-button" :class="{ active: currentPage === pageNumber }">{{ pageNumber }}</button>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- Advertisement Block -->
  <AdvertisementBlock :advertisements="advertisements" :isAdFixed="isAdFixed" />
</template>

<script>
import axios from 'axios';

export default {
  name: 'ListMode',
  props: {
    advertisements: Array,
    isAdFixed: Boolean,
    selectedCity: String,
    selectedTown: String,
  },
  data() {
    return {
      allData: [],         // 所有數據
      currentPage: 1,      // 當前頁碼
      itemsPerPage: 10,    // 每頁顯示數量
      loading: false,      // 是否正在加載
      error: null,         // 錯誤信息
      cityOptions: [],     // 城市選項
      townOptions: [],     // 鄉鎮區選項
      localSelectedCity: '',    // 當前選擇的城市（本地副本）
      localSelectedTown: ''     // 當前選擇的鄉鎮區（本地副本）
    };
  },
  computed: {
    filteredData() {
      const city = this.localSelectedCity;
      const town = this.localSelectedTown;

      // 如果選擇了縣市但未選擇鄉鎮市區，顯示選擇的縣市的所有資料
      if (city && !town) {
        return this.allData.filter(item => item.City === city);
      }
      
      // 如果選擇了鄉鎮市區，過濾出符合縣市和鄉鎮市區的資料
      if (town) {
        return this.allData.filter(item =>
          (!city || item.City === city) &&
          item.Town === town
        );
      }
      
      // 如果未選擇縣市和鄉鎮市區，顯示所有資料
      return this.allData;
    },
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    pagedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredData.slice(start, start + this.itemsPerPage);
    },
    pageNumbers() {
      return Array.from({ length: this.totalPages }, (_, i) => i + 1);
    },
  },
  created() {
    if (!this.selectedCity) {
      this.localSelectedCity = ''; // 預設不選擇任何城市
    }
    if (!this.selectedTown) {
      this.localSelectedTown = ''; // 預設不選擇任何鄉鎮市區
    }
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        this.loading = true;
        const response = await axios.get('https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx');
        this.allData = response.data;
        this.updateCityOptions();
      } catch (error) {
        this.error = '資料取得錯誤，請稍後重試。';
      } finally {
        this.loading = false;
      }
    },
    updateCityOptions() {
      this.cityOptions = [...new Set(this.allData.map(item => item.City))].sort();
      this.updateTownOptions();
    },
    updateTownOptions() {
      if (this.localSelectedCity) {
        this.townOptions = [...new Set(this.allData.filter(item => item.City === this.localSelectedCity).map(item => item.Town))].sort();
        this.localSelectedTown = '';  // 清空選擇的鄉鎮市區
      } else {
        this.townOptions = [];
        this.localSelectedTown = '';  // 清空選擇的鄉鎮市區
      }
    },
    gotoPage(pageNumber) {
      if (pageNumber > 0 && pageNumber <= this.totalPages) {
        this.currentPage = pageNumber;
      }
    },
    openUrl(url) {
      if (url) {
        window.open(url, '_blank');
      }
    },
  },
};
</script>



<style lang="scss" scoped>
.content-area {
  display: flex;
  flex-direction: column; /* 垂直排列 */
}

.filter-container {
  display: flex;
  align-items: center;
  gap: 20px; /* 調整下拉選單之間的間距 */
  margin-bottom: 20px;
}

/* 下拉選單容器樣式 */
.dropdown {
  display: flex;
  align-items: center;
}

/* 下拉選單標籤樣式 */
.dropdown label {
  font-size: 16px;
  margin-right: 10px;
  color: #333;
}

.dropdown select {
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  background-color: #fff;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1); /* 輕微陰影 */
}

/* 下拉選單聚焦樣式 */
.dropdown select:focus {
  outline: none;
  border-color: rgb(12, 164, 211); /* 與按鈕相同的邊框顏色 */
  background-color: #f9f9f9; /* 淺色背景 */
}

/* 下拉選單的選項樣式 */
.dropdown option {
  padding: 8px 12px;
}

/* 下拉選單的選擇高亮 */
.dropdown select:hover {
  background-color: rgb(12, 164, 211); /* 按鈕顏色 */
  color: white; /* 按鈕文字顏色 */
}

.restaurant-container {
  margin-top: 20px; /* 與上方元素之間的間距 */
}

.restaurant-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
}

.location-info {
  display: flex;
  flex-direction: column;
  margin-right: 10px; /* 位置信息的右邊距 */
}

.city-label {
  display: block;
  margin-bottom: 5px;
  background-color: rgb(12, 164, 211);
  color: white;
  padding: 2px 4px;
  border-radius: 4px;
}

.city-label.highlight-city {
  display: block !important;
  white-space: nowrap;
}

.town-label {
  display: block;
  writing-mode: vertical-rl;
  white-space: nowrap;
  color: #999999;
  font-size: 16px;
  margin-right: 10px;
  letter-spacing: 5px;
}

.restaurant-content {
  display: flex;
  align-items: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 100%;
  box-sizing: border-box;
}

.restaurant-image {
  margin-right: 20px;
}

.restaurant-info {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.restaurant-description {
  font-size: 16px;
  line-height: 1.6;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  white-space: normal;
  margin-top: 10px;
}

.restaurant-title {
  margin-bottom: 15px;
}

.restaurant-name {
  font-weight: bold;
  font-size: 26px;
  text-align: left;
  color: black;
  transition: color 0.3s ease;
}

.restaurant-name:hover {
  color: rgb(12, 164, 211);
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px; /* 分頁控件與餐廳列表之間的間距 */
}

.page-info {
  margin-right: 10px;
}

.page-buttons {
  display: flex;
}

.page-button {
  margin: 0 5px;
  padding: 5px 10px;
  border: 1px solid #ccc;
  background-color: #ccc;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.page-button:hover:not(:disabled) {
  background-color: #e0e0e0;
  color: black;
}

.page-button:disabled {
  cursor: not-allowed;
  color: #999;
}

.page-button.active {
  background-color: rgb(12, 164, 211);
  font-weight: bold;
  color: white;
}

.image-container {
  max-width: 200px;
  max-height: 150px;
  overflow: hidden;
  border-radius: 8px; /* 圓角 */
  border: 1px solid #ddd; /* 邊框顏色 */
  box-shadow: 0 2px 5px rgba(0,0,0,0.1); /* 輕微陰影 */
}

.image-container img {
  width: 100%;
  height: auto;
  object-fit: cover; /* 保持圖片比例 */
}

/* 改進加載和錯誤提示 */
.loading-indicator, .error-message {
  font-size: 18px;
  margin: 20px 0;
  text-align: center;
  color: #333;
}
.error-message {
  color: red;
}

</style>
