<template>
  <div class="content-area">
    <!-- 控制選單和模式切換按鈕 -->
    <div class="controls-and-switch">
      <ContentControls
        :cities="cities"
        :towns="towns[selectedCity] || []"
        @city-selected="handleCityChange"
        @town-selected="handleTownChange"
      />
      <div class="mode-switch">
    <span>切換模式:</span>
    <button @click="setCurrentMode('list')" :class="['mode-button', { active: currentMode === 'list' }]">
      <img src="@/assets/list-mode.png" alt="列表模式">
    </button>
    <button @click="setCurrentMode('table')" :class="['mode-button', { active: currentMode === 'table' }]">
      <img src="@/assets/table-mode.png" alt="表格模式">
    </button>
    <button @click="setCurrentMode('card')" :class="['mode-button', { active: currentMode === 'card' }]">
      <img src="@/assets/card-mode.png" alt="卡片模式">
    </button>
  </div>
    </div>

    <!-- 根據模式顯示內容，加入過渡效果 -->
    <transition name="fade">
      <div class="mode-content">
        <ListMode v-if="currentMode === 'list'" :items="filteredItems" :selectedCity="selectedCity" />
        <TableMode v-else-if="currentMode === 'table'" :items="filteredItems" />
        <CardMode v-else-if="currentMode === 'card'" :items="filteredItems" />
      </div>
    </transition>

    <!-- 廣告區塊 -->
    <AdvertisementBlock :advertisements="advertisements" :isAdFixed="isAdFixed" />
  </div>
</template>

<script>
import ListMode from './ListMode.vue';
import TableMode from './TableMode.vue';
import CardMode from './CardMode.vue';
import AdvertisementBlock from './Advertisement.vue';
//import ContentControls from './Controls.vue';
import axios from 'axios';

export default {
  name: 'ContentArea',
  components: {
    ListMode,
    TableMode,
    CardMode,
    AdvertisementBlock,
    //ContentControls,
  },
  props: {
    items: {
      type: Array,
      required: true,
    },
    advertisements: {
      type: Array,
      default: () => [],
    },
    isAdFixed: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentMode: 'list',
      selectedCity: '',
      selectedTown: '',
      cities: [],
      towns: {},
      filteredItems: [],
    };
  },
  created() {
    this.fetchCityAndTownOptions();
  },
  methods: {
    async fetchCityAndTownOptions() {
      try {
        const response = await axios.get("https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx");
        const data = response.data;

        if (data && data.length) {
          this.cities = [...new Set(data.map(item => item.City))];
          this.towns = data.reduce((acc, item) => {
            if (!acc[item.City]) {
              acc[item.City] = [];
            }
            if (!acc[item.City].includes(item.Town)) {
              acc[item.City].push(item.Town);
            }
            return acc;
          }, {});

          if (this.cities.length) {
            this.selectedCity = this.cities[0];
            this.filterItems();
          }
        }
      } catch (error) {
        console.error('獲取城市和鄉鎮選項時發生錯誤:', error);
      }
    },
    handleCityChange(city) {
      this.selectedCity = city;
      this.selectedTown = '';
      this.filterItems();
    },
    handleTownChange(town) {
      this.selectedTown = town;
      this.filterItems();
    },
    filterItems() {
      if (this.selectedCity && this.selectedTown) {
        this.filteredItems = this.items.filter(item =>
          item.City === this.selectedCity && item.Town === this.selectedTown
        );
      } else if (this.selectedCity) {
        this.filteredItems = this.items.filter(item =>
          item.City === this.selectedCity
        );
      } else {
        this.filteredItems = [...this.items];
      }
    },
    setCurrentMode(mode) {
      this.currentMode = mode;
    },
  },
};
</script>

<style scoped>
.content-area {
  width: 100%;
  padding: 20px;
}

.controls-and-switch {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.mode-switch {
  display: flex;
  align-items: center;
}

.mode-switch span {
  margin-right: 10px;
}

.mode-button {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-right: 10px;
}

.mode-button img {
  width: 30px;
  height: 30px;
}

.mode-button.active {
  background-color: #333;
  color: #fff;
}

.mode-content {
  width: 100%;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
