<template>
  <div class="card-mode">
    <!-- Filter Controls -->
    <div class="filter-container">
      <!-- City Filter -->
      <div class="filter-dropdown">
        <select v-model="localSelectedCity" @change="updateTownOptions" aria-label="Select City">
          <option value="" disabled>請選擇縣市</option>
          <option v-for="city in cityOptions" :key="city" :value="city">{{ city }}</option>
        </select>
      </div>
      <!-- Town Filter -->
      <div class="filter-dropdown" >
        <select v-model="localSelectedTown" aria-label="Select Town">
          <option value="" disabled>請選擇鄉鎮市區</option>
          <option v-for="town in townOptions" :key="town" :value="town">{{ town }}</option>
        </select>
      </div>
    </div>

    <!-- Restaurant Cards -->
    <div class="card-container">
      <div v-for="item in pagedData" :key="item.ID" class="restaurant-card" @mouseenter="handleMouseEnter(item.ID)" @mouseleave="handleMouseLeave(item.ID)">
        <!-- Restaurant Image -->
        <div class="restaurant-image">
          <img :src="item.PicURL" alt="Restaurant Image">
          <div class="overlay"></div>
        </div>
        <!-- Restaurant Information -->
        <div class="restaurant-info">
          <div class="location-info">
            <div class="city-label" @mouseenter="handleMouseEnter(item.ID)" @mouseleave="handleMouseLeave(item.ID)">
              {{ item.City }}
            </div>
            <div class="town-label" @mouseenter="handleMouseEnter(item.ID)" @mouseleave="handleMouseLeave(item.ID)">
              {{ item.Town }}
            </div>
          </div>
          <div class="restaurant-name" @mouseenter="handleMouseEnter(item.ID)" @mouseleave="handleMouseLeave(item.ID)" :style="{ color: item.Url ? 'rgb(12, 164, 211)' : 'white' }" @click="openUrl(item.Url)">
            {{ item.Name }}
          </div>
          <div class="description-text" v-show="visibleDescriptions.has(item.ID)">
            {{ truncateText(item.FoodFeature, 35) }}
          </div>
        </div>
      </div>
    </div>

    <!-- Pagination -->
    <div class="pagination">
    <!-- Page Info -->
    <div class="page-info">美食頁次 {{ currentPage }} / {{ totalPages }}</div>
    <!-- Page Buttons -->
    <div class="page-buttons">
      <button v-for="pageNumber in pageNumbers" :key="pageNumber" @click="gotoPage(pageNumber)" :disabled="currentPage === pageNumber" class="page-button" :class="{ active: currentPage === pageNumber }">
        {{ pageNumber }}
      </button>
    </div>
  </div>

    <!-- Error Message -->
    <div v-if="error" class="error-message">{{ error }}</div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'CardMode',
  props: {
    advertisements: Array,
    isAdFixed: Boolean,
    selectedCity: String,
    selectedTown: String,
  },
  data() {
    return {
      allData: [],         // 所有數據
      currentPage: 1,      // 當前頁碼
      itemsPerPage: 10,    // 每頁顯示數量
      loading: false,      // 是否正在加載
      error: null,         // 錯誤信息
      cityOptions: [],     // 城市選項
      townOptions: [],     // 鄉鎮區選項
      localSelectedCity: '',    // 當前選擇的城市（本地副本）
      localSelectedTown: '',     // 當前選擇的鄉鎮區（本地副本）
      visibleDescriptions: new Set() // 顯示描述的 ID 集合
    };
  },
  computed: {
    filteredData() {
      const city = this.localSelectedCity;
      const town = this.localSelectedTown;

      if (city && !town) {
        return this.allData.filter(item => item.City === city);
      }
      
      if (town) {
        return this.allData.filter(item =>
          (!city || item.City === city) &&
          item.Town === town
        );
      }
      
      return this.allData;
    },
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    pagedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredData.slice(start, start + this.itemsPerPage);
    },
    pageNumbers() {
      return Array.from({ length: this.totalPages }, (_, i) => i + 1);
    },
  },
  methods: {
    truncateText(text, length) {
      if (!text) return '';
      return text.length > length ? text.slice(0, length) + '...' : text;
    },

    handleMouseEnter(itemID) {
      this.visibleDescriptions.add(itemID);
    },

    handleMouseLeave(itemID) {
      this.visibleDescriptions.delete(itemID);
    },

    openUrl(url) {
      if (url) {
        window.open(url, '_blank');
      }
    },

    gotoPage(pageNumber) {
      if (pageNumber > 0 && pageNumber <= this.totalPages) {
        this.currentPage = pageNumber;
      }
    },

    async fetchData() {
      try {
        this.loading = true;
        const response = await axios.get('https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx');
        this.allData = response.data;
        this.updateCityOptions();
      } catch (error) {
        this.error = '資料取得錯誤，請稍後重試。';
      } finally {
        this.loading = false;
      }
    },

    updateCityOptions() {
      this.cityOptions = [...new Set(this.allData.map(item => item.City))].sort();
      this.updateTownOptions();
    },

    updateTownOptions() {
      if (this.localSelectedCity) {
        this.townOptions = [...new Set(this.allData.filter(item => item.City === this.localSelectedCity).map(item => item.Town))].sort();
        this.localSelectedTown = '';  // 清空選擇的鄉鎮市區
      } else {
        this.townOptions = [];
        this.localSelectedTown = '';  // 清空選擇的鄉鎮市區
      }
    },
  },
  created() {
    if (!this.selectedCity) {
      this.localSelectedCity = ''; // 預設不選擇任何城市
    }
    if (!this.selectedTown) {
      this.localSelectedTown = ''; // 預設不選擇任何鄉鎮市區
    }
    this.fetchData();
  }
};
</script>



<style lang="scss" scoped>
.card-mode {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.filter-container {
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.filter-dropdown {
  width: 200px;
}

.filter-dropdown select {
  width: 100%;
  padding: 4px 6px;
  border-radius: 4px;
  border: 1px solid #ddd;
  font-size: 12px;
  background-color: #f0f0f0;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  color: #333;
}

.filter-dropdown select:focus {
  border-color: rgb(12, 164, 211);
  outline: none;
  background-color: #ffffff;
}

.filter-dropdown option {
  padding: 4px 6px;
}

.filter-dropdown select:hover {
  background-color: rgb(12, 164, 211);
  color: white;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 默認為每行 2 張卡片 */
  gap: 15px;
  margin-top: 20px;
}

.restaurant-card {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  overflow: hidden;
  transition: box-shadow 0.3s ease;
  cursor: pointer;
  height: 320px; /* 固定卡片高度 */
}

.restaurant-image {
  width: 100%;
  height: 100%; /* 調整高度以適應卡片 */
  overflow: hidden;
  position: relative;
}

.restaurant-image img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* 保持圖片比例並填滿容器 */
  transition: transform 0.3s ease;
}

.restaurant-image:hover img {
  transform: scale(1.3);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s ease;
  opacity: 0; /* 預設為隱藏 */
}

.restaurant-card:hover .overlay {
  opacity: 1;
}

.restaurant-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 8px;
  color: white;
  text-align: left;
  transition: opacity 0.3s ease;
  opacity: 0; /* 預設為隱藏 */
}

.restaurant-card:hover .restaurant-info {
  opacity: 1;
}

.restaurant-description {
  display: flex;
  flex-direction: column;
}

.description-text {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 8px;
  font-size: 12px;
  line-height: 1.4em;
  max-height: 2.8em;
  transition: max-height 0.3s ease;
}

.restaurant-card:hover .description-text {
  -webkit-line-clamp: unset;
  overflow: visible;
  text-overflow: clip;
  max-height: none;
}

.restaurant-name, .city-label, .town-label {
  position: relative;
}

.restaurant-name:hover ~ .description-text,
.city-label:hover ~ .description-text,
.town-label:hover ~ .description-text {
  display: block;
}

.restaurant-name {
  font-weight: bold;
  font-size: 14px;
  color: white;
  cursor: pointer;
  transition: color 0.3s ease;
}

.restaurant-name:hover {
  color: rgb(12, 164, 211);
}

.location-info {
  display: flex;
  gap: 8px;
  margin-bottom: 5px;
}

.city-label, .town-label {
  background-color: rgb(12, 164, 211);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 10px;
}

.town-label {
  background-color: #666;
}

.pagination {
  display: flex;
  flex-direction: row; /* 同一行排列 */
  align-items: center; /* 垂直對齊 */
  justify-content: center; /* 水平置中 */
  gap: 10px; /* 頁碼按鈕之間的間距 */
  margin-top: 20px;
}

.page-info {
  margin-bottom: 10px;
  font-size: 12px;
  color: #333;
}

.page-buttons {
  display: flex;
  gap: 5px;
}

.page-button {
  padding: 4px 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  font-size: 12px;
}

.page-button.active, .page-button:hover {
  background-color: rgb(12, 164, 211);
  color: white;
}

/* 手機模式下隱藏頁次信息 */
@media (max-width: 768px) {
  .page-info {
    display: none; /* 隱藏頁次信息 */
  }
}

.error-message {
  color: red;
  text-align: center;
  margin-top: 20px;
}

/* 響應式設置 */
@media (max-width: 768px) {
  .card-container {
    grid-template-columns: 1fr; /* 每行 1 張卡片 */
  }
  
  .restaurant-card {
    height: auto; /* 自適應高度 */
  }
  
  .restaurant-image {
    height: 200px; /* 調整圖片容器的高度 */
  }
}

/* 確保右側廣告容器在手機模式下顯示 */
.advertisement-container {
  display: flex;
  flex-direction: column; /* 垂直排列 */
  gap: 10px; /* 間距 */
  margin-top: 20px; /* 上間距 */
}

/* 在手機模式下調整右側廣告的樣式 */
@media (max-width: 768px) {
  .advertisement-container {
    display: flex;
    flex-direction: column; /* 垂直排列廣告 */
    width: 100%; /* 確保廣告容器寬度填滿屏幕 */
  }

  .advertisement {
    width: 100%; /* 廣告寬度填滿容器 */
    height: auto; /* 高度自適應 */
  }
}

</style>
